<template>
    <div class="form-group">
        <label>{{label}}</label>
        <input class="form-control" :type="type"
         @input="$emit('input', $event.target.value)"
         :required="required"
         :disabled="disabled"
         :value="value"
         />
    </div>
</template>
<script>
export default{
    name:'AppInput',
    props:{
        label:{
            type:String,
        },
        type:{
            type:String ,
            default:'text',
        },
        required:{
            type:Boolean,
            default:false,
        },
        value:{
            default:'null'
        },
        disabled:{
            type:Boolean,
            default:false,
        }
    }
}
</script>
